	module.exports = {"data":{"post":{"html":"<p>Yesterday was the first day of school in Hamburg after spring vacation, but 20,000 students decided to skip class and protest the war. The march attempted to reach the gates of the US Consulate, and <a title=\"Erst wars friedlich, dann gabs Gewalt - Diskussionen über Polizeieinsatz\" href=\"http://www.abendblatt.de/daten/2003/03/25/137948.html\">things</a> <a title=\"Guardian Unlimited | Special reports | Anti-war protests span the globe\" href=\"http://www.guardian.co.uk/antiwar/story/0,12809,920898,00.html\">got</a> <a title=\"novinite.com\" href=\"http://www.novinite.com/view_news.php?id=20874\">ugly</a>. The consulate was off the approved route, and some rouge protesters did throw stones at police. But does the get-tough stance of Interior Senator 'Judge Merciless' Schill really call for water cannons and bobby sticks to be used on 7th and 8th graders?</p>","fields":{"title":"Trigger Happy on the Water Cannons","date":"25 Mar 2003","url":"https://papascott.de/2003-03-25-trigger-happy-on-the-water-cannons/"}}},"pathContext":{"slug":"/2003-03-25-trigger-happy-on-the-water-cannons/","prev":{"fields":{"title":"Embedded","date":"25 Mar 2003","slug":"/2003-03-25-embedded/"}},"next":{"fields":{"title":"If The Americans Think...","date":"26 Mar 2003","slug":"/2003-03-26-if-the-americans-think/"}}}}

